<template>
  <div class="fillcontain">
    <h3>数据查询</h3>
    <div class="contain">
      <div class="table_container">
        <el-row class="headerSearch">
          <el-select v-model="data.uid" @change="selectGet" filterable placeholder="请选择人员">
            <el-option
              v-for="item in options"
              v-if="item.mode !='1'"
              :key="item._id"
              :label="item.name"
              :value="item._id"
            ></el-option>
          </el-select>
          <el-date-picker
            v-model="value1"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          ></el-date-picker>
          <div class="readWrapper">
            <el-button type="primary" class="search" @click="search">查询</el-button>
          </div>
          <el-select v-model="selectId" @change="selectGet2" filterable placeholder="请选择批次">
            <el-option
              v-for="item in PCoptions"
              :key="item._id"
              :label="item.name"
              :value="item._id"
            ></el-option>
          </el-select>
        </el-row>
        <el-table v-loading="loading" :data="tableData" class="tableDataTop" height="500">
          <el-table-column label="序号" align="center" width="50px">
            <template slot-scope="scope">
               <span>{{scope.$index+(paginations.pageIndex) * paginations.pageSize + 1}} </span>
            </template>
          </el-table-column>
          <el-table-column property="vs" width="200px" label="对阵双方" align="center"></el-table-column>
          <el-table-column
            property="mode"
            width="50px"
            :formatter="formatWQ"
            label="武器"
            align="center"
          ></el-table-column>
          <el-table-column property="sheight" label="发射高度/速度" :formatter="formatWQ1" align="center"></el-table-column>
          <el-table-column property="theight" label="对方高度/速度" :formatter="formatWQ2" align="center"></el-table-column>
          <el-table-column
            property="firedistance"
            label="发射距离"
            :formatter="formatDis"
            align="center"
          ></el-table-column>
          <el-table-column property="hit" :formatter="formatHit" label="命中结果" align="center"></el-table-column>
          <el-table-column
            property="firespeed"
            label="结束弹速/弹目距离"
            :formatter="formatWQ3"
            align="center"
          ></el-table-column>
          <el-table-column property="miss" label="未命中原因" align="center"></el-table-column>
          <el-table-column property="ws" label="误伤" :formatter="formatWS" align="center"></el-table-column>
        </el-table>
        <el-row>
          <el-col :span="24">
            <el-table
              v-loading="loading"
              :data="tableData2"
              border
              stripe
              height="250"
              highlight-current-row
              header-cell-class-name="table-header-class"
              style="width:100%"
            >
              <el-table-column property="user" label="姓名" align="center"></el-table-column>
              <el-table-column property="height" label="高度违规" align="center"></el-table-column>
              <el-table-column property="report" label="报告词违规" align="center"></el-table-column>
              <el-table-column property="fire" label="违规发射" align="center"></el-table-column>
              <el-table-column property="ball" label="突破球" align="center"></el-table-column>
              <el-table-column property="other" label="其他" align="center"></el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import { getUserList } from "@/api/user";
import { batchList, add, modify } from "@/api/batch";
export default {
  data() {
    return {
      fileList: [],
      PCoptions: [],
      data: {
        uid: "",
        name: "",
        site: "2",
        start: "",
        end: ""
      },
      options: this.sessionData("get", "userList"),
      searchUser: [],
      props: { multiple: true, checkStrictly: true },
      value: "",
      value1: "",
      tableData2: [],
      tableData: [],
      loading: false,
      //需要给分页组件传的信息
      paginations: {
        total: 1, // 总数
        pageIndex: 0, // 当前位于哪页
        pageSize: 100, // 1页显示多少条
        pageSizes: [100, 10], //每页显示多少条
        layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
      },
      selectId: "",
      res: null
    };
  },
  created() {},
  mounted() {
    //this.getUserList();
    this.batchList({
      count: this.paginations.pageSize,
      index: this.paginations.pageIndex,
      detail: 1
    });
  },
  methods: {
    formatTime(row) {
      return this.$tool.formatDate(new Date(row.time));
    },
    formatHit(row) {
      return row.hit ? "命中" : "未命中";
    },

    formatWQ(row) {
      return row.mode == 255 ? "航炮-255" : row.mode ? "P-" + row.mode : "";
    },

    formatDis(row) {
      if(row.firedistance){
        return (row.firedistance/1000).toFixed(2)+"km";
      }
      
    },

    formatWQ1(row) {
      var a = row.sheight || "";
      var b = row.sspeed || "";
      var c = a && b ? a + "/" + b : "";
      return c;
    },

    formatWQ2(row) {
      var a = row.theight || "";
      var b = row.tspeed || "";
      var c = a && b ? a + "/" + b : "";
      return c;
    },

    formatWQ3(row) {
      var a = row.fireendspeed || "";
      var b = row.distance || "";
      var c = a && b ? a + "/" + b : "";
      return c;
    },

    formatWS(row) {
      return row.ws ? "是" : "否";
    },

    search() {
      this.PCoptions = [];
      this.selectId = "";
      this.tableData = [];
      this.batchList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        uid: this.data.uid,
        detail: 1
      });
    },
    batchList(obj) {
      batchList(obj).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "获取批次数据成功！",
            type: "success"
          });
          var list = this.$unfly(res.data);
          console.log("批次列表8888", list);
          this.PCoptions = list.array;
          this.resList = list;
        }
      });
    },

    selectGet(vId) {
      let obj = {};
      obj = this.options.find(item => {
        return item._id === vId;
      });
      this.data.name = obj.name;
    },

    selectGet2(id) {
      this.selectTable(id, this.resList);
    },

    selectTable(id, list) {
      var arr = [];
      for (var i in list.array) {
        if (id == list.array[i]._id) {
          for (var j in list.array[i].data) {
            var d = list.array[i].data[j];
            d.pcid = list.array[i]._id;
            for (var k in list.array[i].vs) {
              for (var n in list.array[i].vs[k].sgroup) {
                if (d.sfid == list.array[i].vs[k].sgroup[k].fid) {
                  d.shid = list.array[i].vs[k].sgroup[k].hid;
                  d.suid = list.array[i].vs[k].sgroup[k].uid;
                }
              }
              for (var n in list.array[i].vs[k].tgroup) {
                if (d.tfid == list.array[i].vs[k].tgroup[k].fid) {
                  d.thid = list.array[i].vs[k].tgroup[k].hid;
                  d.tuid = list.array[i].vs[k].tgroup[k].uid;
                }
              }
            }

            var q =
              (list.users[d.suid] || "") +
              "  " +
              (list.users[d.shid] || "") +
              "  " +
              (list.planes[d.sfid] || "");
            var h =
              (list.users[d.tuid] || "") +
              "  " +
              (list.users[d.thid] || "") +
              "  " +
              (list.planes[d.tfid] || "");
            d.vsTitle = q + "VS" + h;
            d.dmjl = ((d.fireend - d.fireestart) * d.firespeed) / 1000;
            d.dmjl = isNaN(d.dmjl) ? 0 : d.dmjl;
            arr.push(d);
          }
        }
      }
      this.tableData = arr;
      var foul = list.array[0].foul;
      var foulArr = [];
      for (var i in foul) {
        foul[i].foul.user = list.users[foul[i].uid];
        foulArr.push(foul[i].foul);
      }
      this.tableData2 = foulArr;
      if (!arr.length) {
        this.$message({
          message: "没有数据！",
          type: "success"
        });
      }
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },

    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    getUserList() {
      let para = {
        limit: this.paginations.pageSize,
        page: this.paginations.pageIndex
      };
      // getUserList(para).then(res => {
      //     // this.loading = false;
      //     // this.paginations.total = res.data.total;
      //     // this.tableData = res.data.userList;
      //     console.log("8888:",res.data.userList)
      // })
    },
    // 每页多少条切换
    handleSizeChange(pageSize) {
      this.paginations.pageSize = pageSize;
      this.getUserList();
    },
    // 上下分页
    handleCurrentChange(page) {
      this.paginations.pageIndex = page;
      this.getUserList();
    }
  }
};
</script>

<style lang="less" scoped>
@import "./table.less";
.headerSearch > div {
  float: left;
}
.search {
  background: #04b3e0;
  border: solid 1px #03d0fb;
  border-radius: 0px;
  margin-left: 15px;
}
.fillcontain > h3 {
  color: #52e5ff;
  font-size: 20px;
  width: 100px;
  font-weight: 500;
  position: relative;
  top: -8px;
  left: 47px;
}
.fillcontain {
  overflow-y: auto;
  padding-bottom: 0;
  background: url("../../assets/img/cbg.png") no-repeat -2px 0px;
  background-size: 100% 100%;

  width: 1648px;
  height: 985px;
  margin: 20px;
}
@media screen and (max-width: 1366px) {
  .fillcontain {
    background: url("../../assets/img/cbg.png") no-repeat 0px 0px;
    background-size: 98% 100%;
    width: 98%;
  }
  .fillcontain > h3 {
    left: 26px;
  }
}
.tableDataTop {
  margin-bottom: 30px;
}
.contain {
  padding: 10px;
  margin-bottom: 20px;
}
.pagination {
  padding: 10px 20px;
  text-align: right;
}
.readWrapper {
  height: 50px;
}
.customWidth {
  width: 50%;
  left: 23%;
}
.inputCascader {
  width: 379px;
  margin-left: 20px;
  margin-right: 20px;
}
/deep/.inputCascader .el-cascader__tags {
  overflow-y: auto;
  height: 28px;
}
.el-table thead.is-group th {
  background: none;
}
.el-table thead.is-group tr:first-of-type th:first-of-type {
  border-bottom: none;
}
.el-table thead.is-group tr:first-of-type th:first-of-type:before {
  content: "";
  position: absolute;
  width: 1px;
  height: 75px; /*这里需要自己调整，根据td的宽度和高度*/
  top: 0;
  left: 0;
  background-color: grey;
  opacity: 0.3;
  display: block;
  transform: rotate(-53deg); /*这里需要自己调整，根据线的位置*/
  transform-origin: top;
}
.el-table thead.is-group tr:last-of-type th:first-of-type:before {
  content: "";
  position: absolute;
  width: 1px;
  height: 75px; /*这里需要自己调整，根据td的宽度和高度*/
  bottom: 0;
  right: 0;
  background-color: grey;
  opacity: 0.3;
  display: block;
  transform: rotate(-54deg); /*这里需要自己调整，根据线的位置*/
  transform-origin: bottom;
  // background:red;
}
</style>



